@import './theme.scss';

.split {
    display: flex;
    flex-direction: row;
    height: 90vh;
    margin-top: 0;
    overflow-x: hidden;
    overflow-y: clip;
}

.gutter {
    @include themify($themes) {
        background-color: themed('gutterBackgroundColor');
        &:hover {
            background-color: themed('gutterHoverBackgroundColor');
        }
    }
    background-repeat: no-repeat;
    background-position: 50%;
    transition: 0.5s;
    z-index: 2;
    flex-basis: 2.4px !important;
}

.gutter.gutter-horizontal {
    background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAUAAAAeCAYAAADkftS9AAAAIklEQVQoU2M4c+bMfxAGAgYYmwGrIIiDjrELjpo5aiZeMwF+yNnOs5KSvgAAAABJRU5ErkJggg==');
    cursor: col-resize;
}