@import '../theme.scss';

.code-panel-header {
    @include themify($themes) {
        border-bottom: 1px solid themed('panelHeaderBorderColor');
        background-color: themed('panelHeaderBackgroundColor');
        height: 60px;
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: center;

        .MuiTabs-root {
            margin-left: 10px;

            .MuiButtonBase-root {
                color: themed('color');
                text-transform: none;
            }

            .MuiButtonBase-root.Mui-selected {
                color: themed('tabSelectedColor')
            }
        }
    }
}

.code-panel-content {
    @include themify($themes) {
        text-align: left;
        padding: 10px;
        color: themed('color');
        overflow-y: auto;
        overflow-x: hidden;
        height: calc(100% - 80px);
        background-color: themed('panelContentBackgroundColor');
    }

    p {
        margin-top: 0px;
    }
}

.__json-pretty__ {
    margin-top: 0px;
    @include themify($themes) {
        background-color: themed('panelContentBackgroundColor');
        color: themed('color');
        .__json-value__ {
            color: themed('jsonValueColor')
        }
        .__json-string__ {
            color: themed('jsonStringColor')
        }
        .__json-boolean__ {
            color: themed('jsonBooleanColor')
        }
    }
}