@import '../theme.scss';

.document-panel-header {
    @include themify($themes) {
        border-bottom: 1px solid themed('panelHeaderBorderColor');
        background-color: themed('panelHeaderBackgroundColor');
        height: 60px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        position: relative;
        min-width: 100px;

        .document-panel-title {
            text-align: left;
            padding-left: 25px;
            display: flex;
            align-items: flex-end;

            .api-title {
                font-size: 14px;
                color: themed('color');
            }

            svg {
                width: 13px;
                font-size: 12px;
                margin-right: 5px;
            }
        }
    }
}

.document-panel-content {
    text-align: left;
    padding: 10px;
    @include themify($themes) {
        color: themed('color');
        background-color: themed('panelContentBackgroundColor');
    }
    overflow-y: auto;
    overflow-x: hidden;
    height: calc(100% - 80px);
    min-width: 100px;

    p {
        margin-top: 0px;
    }
}