@import '../../theme.scss';

.textField {
    .MuiOutlinedInput-root {
        @include themify($themes) {
            background-color: themed('textFieldBackgroundColor');
            width: 100%;
            min-width: 50px;

            fieldset {
                border-color: themed('textFieldBorderColor');
                transition: 0.5s;
            }

            &:hover fieldset {
                border-color: themed('textFieldHoverBorderColor');
            }
        }
    }

    @include themify($themes) {
        .MuiSvgIcon-root {
            color: themed('color')
        }
    
        .MuiOutlinedInput-input {
            color: themed('color')
        }
    
        &::placeholder {
            color: themed('color')
        }
    }

    padding: 10px 15px !important;
    display: block !important;
}