@import '../../../theme.scss';

.MuiAccordionSummary-root {
    @include themify($themes) {
        background-color: themed('panelHeaderBackgroundColor') !important;
        color: themed('color') !important;
        flex-direction: row-reverse;
        border-bottom: 1px solid themed('panelHeaderBorderColor') !important;
        border-top: 1px solid themed('panelHeaderBorderColor') !important;
        
        .MuiAccordionSummary-expandIconWrapper.Mui-expanded {
            transform: rotate(90deg)
        }

        .MuiAccordionSummary-content {
            margin-left: 8px;
            justify-content: space-between;

            .MuiSvgIcon-root {
                font-size: 1.2rem;
                color: themed('infoIconColor');
                align-self: center;
                transition: 0.3s;

                &:hover {
                    color: themed('infoIconHoverColor');
                }
            }

            .MuiTypography-root {
                font-size: 14px;
            }
        }

        .MuiSvgIcon-root {
            color: themed('collapseArrowIconColor');
        }
    }
}