$themes: (
    light: (
        gutterBackgroundColor: rgb(182, 182, 182),
        gutterHoverBackgroundColor: rgb(13, 147, 242),
        panelHeaderBorderColor: rgba(0, 0, 0, 0.08),
        panelHeaderBackgroundColor: rgb(244, 244, 245),
        panelContentBackgroundColor: rgb(250, 250, 250),
        apiItemButtonHoverBackgroundColor: rgba(226, 226, 226, 0.52),
        apiItemButtonSelectedBackgroundColor: rgba(0, 0, 0, 0.1),
        slideButtonColor: rgb(78, 78, 78),
        slideButtonBackgroundColor: white,
        slideButtonHoverColor: rgb(117, 179, 240),
        slideButtonHoverBackgroundColor: rgb(117, 179, 240),
        modalBackgroundColor: white,
        modalButtonColor: white,
        modalButtonBackgroundColor: rgb(74, 144, 226),
        modalButtonHoverBackgroundColor: rgb(117, 179, 240),
        modalBackdropColor: rgba(105, 140, 205, 0.1),
        textFieldBackgroundColor: white,
        textFieldBorderColor: rgba(0, 0, 0, 0.08),
        textFieldHoverBorderColor: rgb(117, 179, 240),
        infoIconColor: rgb(74, 144, 226),
        infoIconHoverColor: rgb(117, 179, 240),
        collapseArrowIconColor: rgba(0, 0, 0, 0.3),
        tabSelectedColor: rgb(24, 144, 255),
        jsonValueColor: rgb(177, 102, 56),
        jsonStringColor: rgb(73, 132, 94),
        jsonBooleanColor: rgb(103, 133, 255),
        color: rgb(78, 78, 78)
    ),
    dark: (
        gutterBackgroundColor: #eee,
        gutterHoverBackgroundColor: rgb(13, 147, 242),
        panelHeaderBorderColor: rgb(52, 52, 55),
        panelHeaderBackgroundColor: rgb(39, 39, 43),
        panelContentBackgroundColor: rgb(29, 29, 32),
        apiItemButtonHoverBackgroundColor: rgb(4, 18, 37),
        apiItemButtonSelectedBackgroundColor: rgb(44, 58, 77),
        slideButtonColor: rgb(210, 210, 210),
        slideButtonBackgroundColor: white,
        slideButtonHoverColor: rgb(117, 179, 240),
        slideButtonHoverBackgroundColor: rgb(117, 179, 240),
        modalBackgroundColor: rgb(29, 29, 32),
        modalButtonColor: white,
        modalButtonBackgroundColor: rgb(74, 144, 226),
        modalButtonHoverBackgroundColor: rgb(117, 179, 240),
        modalBackdropColor: rgba(105, 140, 205, 0.1),
        textFieldBackgroundColor: rgb(48, 49, 56),
        textFieldBorderColor: rgb(60, 61, 66),
        textFieldHoverBorderColor: rgb(117, 179, 240),
        infoIconColor: rgb(74, 144, 226),
        infoIconHoverColor: rgb(117, 179, 240),
        collapseArrowIconColor: rgb(95, 95, 98),
        tabSelectedColor: rgb(24, 144, 255),
        color: rgb(255, 255, 255, 0.87)
    )
);

@mixin themify($themes: $themes) {
    @each $theme,
    $map in $themes {
        .theme-#{$theme} & {
            $theme-map: () !global;

            @each $key,
            $submap in $map {
                $value: map-get(map-get($themes, $theme), '#{$key}');
                $theme-map: map-merge($theme-map, ($key: $value)) !global;
            }

            @content;
            $theme-map: null !global;
        }
    }
}

@function themed($key) {
    @return map-get($theme-map, $key);
}