@import '../theme.scss';

.panel-header {
    @include themify($themes) {
        border-bottom: 1px solid themed('panelHeaderBorderColor');
        background-color: themed('panelHeaderBackgroundColor');
    }
}

.panel-content {
    overflow-y: auto;
    overflow-x: hidden;
    height: calc(100% - 60px);
    @include themify($themes) {
        background-color: themed('panelContentBackgroundColor');
    }
}

.panel {
    position: relative;
}

.api-item-button {
    min-height: 32px;
    padding: 0;
    @include themify($themes) {
        color: themed('color');
        &:hover {
            background-color: themed('apiItemButtonHoverBackgroundColor') !important;
        }
        &.Mui-selected {
            background-color: themed('apiItemButtonSelectedBackgroundColor') !important;
        }
        svg {
            width: 13px;
        }
    }
    padding-left: 20px;
    height: 36px;
}

.slide-button {
    position: absolute !important;
    transform: translate(15px, 20px) !important;
    top: 0px !important;
    right: 1px !important;
    border-radius: 50% !important;
    width: 24px !important;
    height: 24px !important;
    border: 1px solid !important;
    border-color: black !important;
    @include themify($themes) {
        color: themed('slideButtonColor') !important;
        background-color: themed('slideButtonBackgroundColor') !important;
        &:hover {
            color: themed('slideButtonHoverColor') !important;
            border-color: themed('slideButtonHoverBackgroundColor') !important;
        }
    }
    transition: color 2s;
}

.MuiBackdrop-root {
    @include themify($themes) {
        background-color: themed('modalBackdropColor');
    }
}

.MuiDialog-container {
    .MuiDialog-paper {
        @include themify($themes) {
            background-color: themed('modalBackgroundColor');
            color: themed('color');
            .MuiDialogContentText-root {
                color: themed('color');
            }
            button {
                color: themed('modalButtonColor');
                background-color: themed('modalButtonBackgroundColor');
                margin-right: 20px;
                text-transform: none;
                &:hover {
                    background-color: themed('modalButtonHoverBackgroundColor');
                }
            }
        }
    }
}