@import '../../../theme.scss';

.MuiCollapse-root {
    .MuiAccordionDetails-root {
        @include themify($themes) {
            padding: 0;
            background-color: themed('panelHeaderBackgroundColor');
            color: themed('color');

            .MuiListItemIcon-root {
                min-width: auto;
                margin-right: 5px;
                .MuiSvgIcon-root {
                    font-size: 12px;
                }
            }

            .MuiListItemText-primary {
                font-size: 14px;
            }
        }
    }
}